import React from "react";

const RideCard = () => {
    return(
        <div className="rideCard flex">
            <h1>Name</h1>
            <div className="cardDetails flex">
                <div className="firstPart">
                    From<br/>
                    <label id="from">Delhi</label><br/><br/>
                    Date<br/>
                    <label id="date">01/05/2022</label><br/><br/>
                    Price<br/>
                    <label id="price">5000 &#8377; </label><br/>
                </div>
                <div className="secondPart">
                    To<br/>
                    <label id="to">Mumbai</label><br/><br/>
                    Time<br/>
                    <label id="time">6pm-9pm</label><br/><br/>
                    Seat Availablity<br/>
                    <label id="seats">2</label><br/>
                </div>
            </div>
        </div>
    )
}

export default RideCard;